/** @jsx jsx */
import { jsx } from "theme-ui";
import { useState } from "react";

import { connectSearchBox } from "react-instantsearch-dom";
import SearchIcon from "@components/icons/search";
import ClearIcon from "@components/icons/cross";
import ArrowIcon from "@components/icons/arrowRight";
import { rem } from "@src/theme";
import { navigate } from "gatsby";
interface ComponentProps {
  currentRefinement?: string;
  refine?: (query) => void;
  submitMode: boolean;
}
export const SearchBox = ({
  currentRefinement,
  refine,
  submitMode,
}: ComponentProps) => {
  const [staticValue, setStaticValue] = useState("");
  const onInputChange = (val) => {
    if (submitMode) {
      setStaticValue(val);
    } else {
      refine(val);
    }
  };
  return (
    <form
      noValidate
      action=""
      role={submitMode ? "form" : "search"}
      sx={{
        border: "2px solid #20222B",
        position: "relative",
        height: rem(61),
        width: ["100%", null, rem(560)],
        maxWidth: rem(560),
        bg: "white",
        display: "flex",
        alignItems: "center",
      }}
      onSubmit={(e) => {
        e.preventDefault();
        if (submitMode) {
          navigate(`/search?query=${encodeURI(staticValue)}`);
        }
      }}
    >
      <span
        sx={{
          position: "absolute",
          left: rem(10),
          top: rem(18),
        }}
      >
        <SearchIcon />
      </span>
      <input
        sx={{
          pl: rem(35),
          fontSize: rem(20),
          appearance: "none",
          "-moz-appearance": ["none"],
          "-webkit-appearance": ["none"],
          height: "100%",
          border: "none",
          flex: 1,
          fontWeight: 600,
          "&:focus": {
            outline: "none",
          },
        }}
        value={currentRefinement}
        onChange={(event) => onInputChange(event.currentTarget.value)}
      />
      {currentRefinement ? (
        <button
          sx={{
            pr: rem(20),
          }}
          onClick={(e) => {
            e.preventDefault();
            refine("");
          }}
        >
          <ClearIcon />
        </button>
      ) : submitMode ? (
        <button
          type="submit"
          sx={{
            pr: rem(20),
          }}
        >
          <ArrowIcon />
        </button>
      ) : null}
    </form>
  );
};

// 2. Connect the component using the connector
const CustomSearchBox = connectSearchBox(SearchBox);

// 3. Use your connected widget
export default CustomSearchBox;
